import { Component ,ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams,Platform,Tabs } from 'ionic-angular';
import { LivestockProvider } from "../../providers/livestock/livestock";
import { StatusBar } from '@ionic-native/status-bar';
import { SettingProvider } from "../../providers/setting/setting";
import { BackButtonService } from '../../services/backButton';
import { LivestockdetailPage } from '../livestock/livestockdetail';

/**
 * Generated class for the SearchlivestockPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-searchlivestock',
  templateUrl: 'searchlivestock.html',
})
export class SearchlivestockPage {
  @ViewChild('myTabs') tabRef: Tabs;
  result :any[];
  page:number=1;//当前页码
  total:number=0;//总页码
  constructor(public navCtrl: NavController, public settingProvider:SettingProvider,public navParams: NavParams, public platform:Platform,public statusBar: StatusBar,private backButtonService:BackButtonService,public livestockProvider:LivestockProvider) {
  		platform.ready().then(() => {
	        this.backButtonService.registerBackButtonAction(this.tabRef);
	        statusBar.backgroundColorByHexString("#1ab394");
	        statusBar.show();
      	});
      	console.log(navParams.data.id,this.settingProvider.user.areaId);
      	/*搜索*/
      	let _areaId = navParams.data.id == 0 ? this.settingProvider.user.areaId : navParams.data.id;
      	if(navParams.data.keyword!="") {
	        this.settingProvider.keyword = navParams.data.keyword;
	        this.livestockProvider.doSearchLivestockPage(this.settingProvider.keyword,_areaId).subscribe((res) => {
	          this.result = res["data"]["list"];
	        }, (err) => {
	         this.settingProvider.dismissLoading();
	         this.settingProvider.presentAlert("貌似网络出了点小差～",'');
	        });
	    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchlivestockPage');
  }

  selectItem(item){
  	 this.navCtrl.push(LivestockdetailPage,{item:item,type:"livesockSearch"});
  }
  
  doSearch() {
      if(this.settingProvider.keyword!=""){
      	  let _areaId = this.navParams.data.id == 0 ? this.settingProvider.user.areaId : this.navParams.data.id;
          this.livestockProvider.doSearchLivestockPage(this.settingProvider.keyword , _areaId).subscribe((res) => {
            this.result = res["data"]["list"];
          }, (err) => {
           this.settingProvider.dismissLoading();
           this.settingProvider.presentAlert("貌似网络出了点小差～",'');
          });
      }
  }

   doInfinite(infiniteScroll) {
      if(this.total > this.page || this.total == 0 ){
          this.page = this.page + 1;
    	  let _areaId = this.navParams.data.id == 0 ? this.settingProvider.user.areaId : this.navParams.data.id;
          this.livestockProvider.doSearchLivestockPage(this.settingProvider.keyword,_areaId,this.page).subscribe((res) => {
            if(res["code"]==10000){
                let row:any[] = res["data"]["list"];
                for (var i = row.length - 1; i >= 0; i--) {
                   this.result.push(row[i]);
                 } 
                this.total = res["data"]["total"];
                console.log("total",this.total);
                infiniteScroll.complete();
              }else{
                 this.settingProvider.presentAlert(res["msg"],'');
              }
          }, (err) => {
           this.settingProvider.dismissLoading();
           this.settingProvider.presentAlert("貌似网络出了点小差～",'');
        });
      }else{
          infiniteScroll.complete();
      }
  }
}



