import { Component ,ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams ,App,Platform,Tabs} from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { TabsPage } from '../tabs/tabs';
import { BackButtonService } from '../../services/backButton';


/**
 * Generated class for the LivestockPage page.
 *import citise from '../../assets/cities.json';
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-livestockdetail',
  templateUrl: 'livestockdetail.html',
})
export class LivestockdetailPage {
  @ViewChild('myTabs') tabRef: Tabs;
  ltock:any;
  constructor(public appCtrl:App,public navCtrl: NavController, public navParams: NavParams,private backButtonService:BackButtonService, public platform:Platform,public statusBar: StatusBar) {
      platform.ready().then(() => {
        this.backButtonService.registerBackButtonAction(this.tabRef);
        statusBar.backgroundColorByHexString("#1ab394");
        statusBar.show();
      });
      this.ltock = navParams.data.item;
      console.log(this.ltock);
  }

  /*自定义返回键*/
  goBack(){
    this.navCtrl.setRoot(this.appCtrl.getRootNav().root);
  }
}



