import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,App,Platform} from 'ionic-angular';
import { SettingProvider } from "../../providers/setting/setting";
import { HomePage } from '../home/home';
import { StatusBar } from '@ionic-native/status-bar';

/**
 * Generated class for the SectionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-section',
  templateUrl: 'section.html',
})
export class SectionPage {
  constructor(public appCtrl: App,public navCtrl: NavController, public navParams: NavParams,public settingProvider:SettingProvider, public platform:Platform,public statusBar: StatusBar) {
      platform.ready().then(() => {
        statusBar.backgroundColorByHexString("#1ab394");
        statusBar.show();
      });
  }

  ionViewDidLoad() {
    console.log(this.settingProvider.cityColumns);
  }

  selsection(a:any){
      this.settingProvider.homeArea = a;
  		this.navCtrl.pop();
  }
}



