import { Component ,ViewChild} from '@angular/core';
import { NavController ,App,Platform,NavParams} from 'ionic-angular';
import { BackButtonService } from '../../services/backButton';

import { StatusBar } from '@ionic-native/status-bar';
import { SearchPage } from '../search/search';
import { LoginPage } from '../login/login';
import { SectionPage } from '../section/section';
import { Storage } from '@ionic/storage';
import { SettingProvider } from "../../providers/setting/setting";
import { SearchlivestockPage } from '../searchlivestock/searchlivestock';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  @ViewChild('keyword') keywordInput: HTMLInputElement;
  homeSearchType:string = "farmer";
  constructor(public navParams: NavParams,public navCtrl: NavController,private statusBar:StatusBar,private platform:Platform,public appCtrl:App,public storage:Storage,public settingProvider:SettingProvider,private backButtonService:BackButtonService) {
    platform.ready().then(() => {
        this.backButtonService.registerBackButtonAction(null);
    });
    statusBar.backgroundColorByHexString("#1ab394");
    this.statusBar.overlaysWebView(false);
    this.statusBar.show();
  }

  ionViewDidEnter(){
    if( this.settingProvider != null ){
      if ( this.settingProvider.user != null ){
        if ( (this.settingProvider.user as any).areaId > 0 ){
        this.settingProvider.getEpidemicPrevention((this.settingProvider.user as any).areaId).subscribe((res) => {
          if(res["code"] == 10000){
               this.settingProvider.epidemicPrevention = res["data"];
           }
        }, (err) => {
         console.log(err);
        });
       }
      }
    }
  }

  /*section*/
  section(){
        this.appCtrl.getRootNav().push(SectionPage);
  }

  /*选择*/
  ionChangeSelect(){
      if(this.homeSearchType == "livesock"){
        this.keywordInput.placeholder = "按牲畜编号进行搜索";
      }else{
        this.keywordInput.placeholder = "按农户名称或者4位身份证尾数搜索";
      }
      console.log(this.keywordInput);
      console.log("你选择了",this.homeSearchType);
  }
  /*搜索*/
  doSearch(keyword: HTMLInputElement){

    let id = this.settingProvider.homeArea.value > 0 ? this.settingProvider.homeArea.value : 0;
    
    if(keyword.value.length == 0){
       this.settingProvider.presentAlert("请输入关键词","");
    }else{
      if(this.homeSearchType == "farmer"){
        this.appCtrl.getRootNav().push(SearchPage,{keyword:keyword.value,id:id,searchType:"homeSearch"});
      }else{
        this.appCtrl.getRootNav().push(SearchlivestockPage,{keyword:keyword.value,id:id,searchType:"homeSearch"});
      }
  		
  	}
  }

}



